import React from "react";

function LeftSideBar({ prodectArray, getRenderOption }) {
  const handleClick = (option) => {
    if (option == 1) {
      getRenderOption(1);
    }
    if (option == 2) {
      getRenderOption(2);
    }
    if (option == 3) {
      getRenderOption(3);
    }
    if (option == 4) {
      getRenderOption(4);
    }
    if (option == 5) {
      getRenderOption(5);
    }
  };
  return (
    <div>
      Item Group
      {prodectArray.map((item, idx) => {
        return (
          <div key={idx}>
            <button
              style={{ width: "100%", textAlign: "left" }}
              onClick={() => handleClick(idx + 1)}
            >
              {item}
            </button>
          </div>
        );
      })}
    </div>
  );
}

export default LeftSideBar;
