import React, { useEffect, useState } from "react";
import Header from "./components/Header";
import LeftSideBar from "./components/LeftSideBar";
import SearchBar from "./components/SearchBar";
import Menu from "./components/Menu";

import "./styles.css";
import Menu1 from "./components/Menu1";
import Menu2 from "./components/Menu2";
import Menu4 from "./components/Menu4";

function App() {
  const [productCollection1, setProductCollect1] = useState([]);
  const [productCollection2, setProductCollect2] = useState([]);
  const [productCollection3, setProductCollect3] = useState([]);
  const [productCollection4, setProductCollect4] = useState([]);
  const [productCollection5, setProductCollect5] = useState([]);

  const [show1, setShow1] = useState(false);
  const [show2, setShow2] = useState(false);
  const [show3, setShow3] = useState(false);
  const [show4, setShow4] = useState(false);
  const [show5, setShow5] = useState(false);

  useEffect(() => {
    fetch(`https://www.thecocktaildb.com/api/json/v1/1/filter.php?c=Cocktail`)
      .then((response) => response.json())
      .then((res) => setProductCollect1(res))
      .catch((error) => console.log("The error", error));
  }, []);

  const prodectArray = ["RoyalStag", "Rum", "Wiskey", "Bear", "Water"];
  function getRenderOption(option) {
    console.log(option);
    if (option == 1) {
      setShow1(true);
      setShow2(false);
      setShow3(false);
      setShow4(false);
      setShow5(false);
    }
    if (option == 2) {
      setShow1(false);
      setShow2(true);
      setShow3(false);
      setShow4(false);
      setShow5(false);

      fetch(
        `https://www.thecocktaildb.com/api/json/v1/1/filter.php?c=Ordinary_Drink`
      )
        .then((response) => response.json())
        .then((res) => setProductCollect2(res))
        .catch((error) => console.log(error));
    }
    if (option == 3) {
      setShow1(false);
      setShow2(false);
      setShow3(true);
      setShow4(false);
      setShow5(false);

      fetch(
        `https://www.thecocktaildb.com/api/json/v1/1/filter.php?a=Non_Alcoholic`
      )
        .then((response) => response.json())
        .then((res) => setProductCollect3(res))
        .catch((error) => console.log(error));
    }
    if (option == 4) {
      setShow1(false);
      setShow2(false);
      setShow3(false);
      setShow4(true);
      setShow5(false);

      fetch(
        `https://www.thecocktaildb.com/api/json/v1/1/filter.php?g=Champagne_flute`
      )
        .then((response) => response.json())
        .then((res) => console.log(res))
        .catch((error) => console.log(error));
    }
    if (option == 5) {
      setShow1(false);
      setShow2(false);
      setShow3(false);
      setShow4(false);
      setShow5(true);
    }
  }
  return (
    <>
      <div className="App">
        <div
          className="firstChild"
          style={{
            width: "100%",
            height: "100px",
            background: "#a0a0a0"
          }}
        >
          <Header />
        </div>
        <div
          className="secondChild"
          style={{
            display: "grid",
            gridTemplateColumns: "auto auto"
          }}
        >
          {" "}
          <div style={{ height: "auto", background: "green" }}>
            <LeftSideBar
              prodectArray={prodectArray}
              getRenderOption={getRenderOption}
            />
          </div>
          <div style={{ height: "auto", background: "#e0e0e0" }}>
            <SearchBar />
            {show1 && <Menu productCollection1={productCollection1} />}
            {show2 && <Menu1 productCollection2={productCollection2} />}
            {show3 && <Menu2 productCollection3={productCollection3} />}
            {show4 && <Menu3 productCollection4={productCollection4} />}
            {show5 && <Menu4 productCollection5={productCollection5} />}
          </div>
        </div>
      </div>
    </>
  );
}
export default App;
