import React, { useEffect, useState } from "react";

function Menu1({ productCollection2 }) {
  console.log(productCollection2);
  const [isShowUi, setIsShowUi] = useState(false);
  const [productArr, setProductArr] = useState([]);
  const { drinks } = productCollection2;
  useEffect(() => {
    setIsShowUi(true);
    //console.log( drinks)
    if (drinks) {
      setProductArr(Object.entries(drinks));
      console.log(productArr);
    }
  }, [drinks]);

  if (productArr.length < 0) {
    return <div></div>;
  }
  if (productArr.length > 0) {
    return (
      <div>
        {productArr.map((item, idx) => {
          return (
            <div
              style={{
                display: "flex",
                flexDirection: "column",
                justifyContent: "center"
              }}
            >
              <h3 style={{ background: "red" }}>{item[1].strDrink}</h3>
              <img
                src={item[1].strDrinkThumb}
                style={{ width: "100px", marginLeft: "0%" }}
              />
            </div>
          );
        })}
      </div>
    );
  } else {
    return <h1>hello</h1>;
  }
}
export default Menu1;
